@import "sass/index";

$defaultTreeWidth: 400px;
$mobileTreeWidth: 250px;
$defaultTreeHeight: 150px;
$mobileTreeHeight: 85px;
$defaultTreeMargin: 260px;
$collapseTreeMargin: 185px;
$mobileTreeMargin: 0;

$branchWidth: 215px;
$branchMargin: 100px;

$marginBetween: 300px;

$collapseWidth: (
  $defaultTreeWidth + $defaultTreeMargin + $branchMargin + $branchWidth +
    $marginBetween
);

.root {
  position: fixed;
  z-index: 1202;
  top: 0;

  width: 100%;
  pointer-events: none;

  display: flex;
  align-items: flex-start;
}

.tree {
  width: $defaultTreeWidth;
  height: $defaultTreeHeight;
  margin-left: $defaultTreeMargin;
  @media (max-width: $collapseWidth) {
    margin-left: $collapseTreeMargin;
  }

  @media (max-width: $mobileScreen) {
    margin-left: $mobileTreeMargin;
  }

  @media (max-width: 500px) {
    display: none;
  }
}
.branch {
  position: absolute;
  width: $branchWidth;
  right: $desktopChatWidth + $branchMargin;

  @media (max-width: $collapseWidth) {
    right: $branchMargin;
  }

  @media (max-width: $tabletScreen) {
    display: none;
  }
}
